import React, { Component } from 'react';
import Script from 'react-load-script';
import './chordRecognizer.scss';
import './fft.js';


export default class ChordRecognizer extends Component {
    componentDidMount() {
        const audioCtx = new AudioContext();


        //Create 2D canvas
        const canvas = document.createElement('canvas');
        canvas.id = "oscilloscope";
        canvas.style.position = 'absolute';
        canvas.style.top = 0;
        canvas.style.left = 0;
        canvas.width = 800;
        canvas.height = 600;
        document.getElementById("chordRecognizer").appendChild(canvas);
        const canvasCtx = canvas.getContext('2d');
        canvasCtx.clearRect(0, 0, canvas.width, canvas.height);

        var analyser = audioCtx.createAnalyser();


        const handleSuccess = function(stream) {
            const source = audioCtx.createMediaStreamSource(stream);

            source.connect(analyser);

            analyser.fftSize = 4096;
            var bufferLength = analyser.frequencyBinCount;
            var dataArray = new Uint8Array(bufferLength);
            analyser.getByteTimeDomainData(dataArray);


            // draw an oscilloscope of the current audio source

            let draw = function () {

            requestAnimationFrame(draw);

            analyser.getByteTimeDomainData(dataArray);

            canvasCtx.fillStyle = "rgb(200, 200, 200)";
            canvasCtx.fillRect(0, 0, canvas.width, canvas.height);

            canvasCtx.lineWidth = 2;
            canvasCtx.strokeStyle = "rgb(0, 0, 0)";

            canvasCtx.beginPath();

            var sliceWidth = canvas.width * 1.0 / bufferLength;
            var x = 0;

            // console.log(dataArray[0]);

            for (var i = 0; i < bufferLength; i++) {

                var v = dataArray[i] / 128.0;
                var y = v * canvas.height / 2;

                if (i === 0) {
                    canvasCtx.moveTo(x, y);
                } else {
                    canvasCtx.lineTo(x, y);
                }

                x += sliceWidth;
            }

            canvasCtx.lineTo(canvas.width, canvas.height / 2);
            canvasCtx.stroke();
            }

            draw();
            // const processor = audioCtx.createScriptProcessor(1024);
            // console.log(source);
            // console.log(processor);
            // source.connect(processor);
            // processor.connect(audioCtx.destination);
            //
            // processor.onaudioprocess = function(audioProcessingEvent) {
            //     // console.log(e);
            //     let bufferSource = audioCtx.createBufferSource();
            //     // set the buffer in the AudioBufferSourceNode
            //     bufferSource.buffer = audioProcessingEvent.inputBuffer;
            //
            //     // connect the AudioBufferSourceNode to the
            //     // destination so we can hear the sound
            //     bufferSource.connect(audioCtx.destination);
            //     //
            //     // // start the source playing
            //     bufferSource.start();
            //
            //     //Create analyser node
            //     const analyserNode = audioCtx.createAnalyser();
            //     analyserNode.fftSize = 2048;
            //     const bufferLength = analyserNode.frequencyBinCount;
            //     const dataArray = new Float32Array(bufferLength);
            //
            //     //Set up audio node network
            //     bufferSource.connect(analyserNode);
            //     analyserNode.connect(audioCtx.destination);
            //
            //     //Get spectrum data
            //     analyserNode.getFloatFrequencyData(dataArray);
            //     // console.log(dataArray);
            //     // draw(dataArray);
            // };
        };

        navigator.mediaDevices.getUserMedia({
                audio: true,
                video: false
            })
            .then(handleSuccess);



    }

    render() {
        return (
                <div className="chordRecognizer" id="chordRecognizer">
                </div>
            );
    }
}
